#Home {
	max-width: 1000px;
	padding: 16px;
	margin: 0 auto;

	> header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 8px 16px;
		border-bottom: 1px solid #ddd;
	}

	> main {
		padding: 16px 0;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}
}
